@import "./settings";

body {
    padding: 0px;
    margin: 0px;
    font-family: Arial, Helvetica, sans-serif;
    background-color: #eeeeee;
    overflow: hidden;
}

#none {
    display: none;
    text-align: center;
    h1 {
        margin: 30px auto;
    }
}

@media screen and (max-width: 961px) {
    #root {
        display: none;
    }
    #none {
        display: block;
    }
}

.loading {
    height: 20px;
    animation-name: loading;
    animation-timing-function: linear;
    animation-duration: 0.5s;
    animation-iteration-count: infinite;
}

@keyframes loading {
    from {
        transform: rotateZ(0deg);
    }
    to {
        transform: rotateZ(360deg);
    }
}

.black {
    color: black;
}
.grey {
    color: $grey_text;
}

.pageBody {
    overflow-y: scroll;
    overflow-x: hidden;
    height: 100vh;
}

* {
    box-sizing: border-box;
    user-select: none;
}

.pageBody {
    width: 100%;
}

span {
    font-family: Century Gothic, regular;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: Calibri;
}

input,
select {
    font-family: Lucida Sans Unicode, regular;
}

button:focus {
    outline: none;
}
